* {
  margin: 0;
  padding: 0;
  border: 0;
  outline: 0;
  box-sizing: 0;
  list-style: none;
  text-decoration: none;
  color:var(--primario)
}
:root {
  --primario: #131112;
  --fondo: #d3e1fa;
  --activo: #4db5ff;
}

.body {
  background: var(--fondo);
}
section {
  background: var(--fondo);
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  padding: 1rem;
  flex-wrap: wrap;
}
.versiculo {
  font-size: 1.2rem;
  font-weight: bold;
  font-style: italic;
  color: var(--primario);
}
.versiculo-cita {
  font-size: 1.1rem;
  font-weight: bold;
  font-style: italic;
  color: var(--primario);
  text-align: center;
}
p {
  color: var(--primario);
  font-size: 1.065rem;
  margin-top: 0.2rem;
}
a{
  color: var(--primario);
  font-weight:bolder ;
  text-decoration: none;
}
.stack {
  margin: 1rem;
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  align-items: center;
  justify-content: center;
  gap: 0.5rem;
}
.instrucciones {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  background: var(--fondo);
  position: fixed;
  z-index: 1;
  top: 0;
  padding: 0.5rem;
  width: 80%;
  border-radius: 2rem;
  border: 1px solid var(--primario);
}
