  display: flex;
  flex-direction: column;
  flex-wrap: wrap;
  justify-content: center;
  
} */
.preguntas {
  margin-top: 2rem;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: left;
}


.respuesta{
  flex-grow: 4;
  min-width: 200px;
  color: var(--activo);
}

.pregunta h3 {
  font-size: 0.7rem;
  margin: 0.5rem;
  color: var(--primario);
}
.input-range {
  padding: 0.5rem;
  max-width: 100px;
}
.results {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  flex-wrap: wrap;
  gap: 0.5rem;
  margin-top: 3rem;
}
.justify-right {
  text-align: right;
}

.dones {
  margin-top: 3rem;
  width: 100%;
}
.don {
  border: 1px solid var(--primario);
  border-radius: 1rem;
  padding: 0.5rem;
  margin: 0.5rem;
  width: fit-content;
  background-color: var(--fondo);
  display: flex;
  gap:1rem;
}
.boton {
  padding: 0.5rem;
  max-width: 350px;
  background-color: var(--activo);
  color: var(--fondo);
  justify-self: center;
  border-radius: 1rem;
  text-align: center;
  font-weight: bold;
}
.boton:hover {
  background-color: var(--fondo);
  color: var(--activo);
  border: 1px solid var(--activo);
}
.resultados{
  display: flex;
  flex-direction: column;
  gap:1rem;
  justify-content: center;
}
.profile-picture {
  border-radius: 50%; /* Make the image circular */
  object-fit: contain; /* Make the image contained */
  height: 100%; /* Set the height of the image to 100% of its parent */
  width: 100%; /* Set the width of the image to 100% of its parent */
  max-height: 2rem ;
}
.pagination {
  display: flex;
  align-items: center;
}

.pagination button {
  background-color: white;
  color: --var(primario);
  border: 2px solid var(--primario);
  border-radius: 50%;
  width: 30px;
  height: 30px;
  margin: 5px;
}

.pagination button.active {
  background-color: var(--activo);
  color: white;
}

.pagination span {
  margin: 5px;
}
.pagination button.next {
  border: none;
  background-color: transparent;
  width: auto;
  height: auto;
}

.pagination button.next:before {
  content: "";
  display: inline-block;
  width: 0;
  height: 0;
  border-top: 10px solid transparent;
  border-bottom: 10px solid transparent;
  border-left: 15px solid var(--activo);
}

.buscar-don {
  background-color: blue;
  color: white;
  border: none;
  padding: 10px 20px;
  font-size: larger;
}

.buscar-don i {
  margin-right: 5px;
}

@media screen and (min-width: 800px) {
  
  .pregunta {
    display: flex;
    flex-direction: column;
    font-size: 1rem;
    max-width: 250px;
  }
  .dones {
    display: grid;
    grid-template-columns: auto auto auto;
    justify-content: space-between;
    width: 100%;
  }
  .pregunta h3 {
    font-size: 1rem;
    margin: 0.5rem;
    color: var(--primario);
  }
  .input-range {
    max-width: 100px;
  }
  .boton {
    max-width: 100%;
  }
}
.icon {
  width: 5rem;
  height: 5rem;
  /* margin-right: 0.5rem; */
}
